                                        <tr role="row" class="odd" >                                                
                                            <td v-if="itemModal.acao == 'baixar'">
                                                <div class="form-control" style="padding: 0px; border: 0px; height: 0px;">
                                                    <input @change="soma_parcelas_selecionadas_baixa()" type="checkbox" :id="'confirmar_taxas'+parcela.id" v-model="parcela.pagar" :disabled="parcela.quitado" >
                                                    <label :for="'confirmar_taxas'+parcela.id" class="control-label">&nbsp;</label>                                                    
                                                </div>
                                            </td>
                                            <td>{{parcela.parcela}} </td>
                                            <td style="white-space: nowrap">{{parcela.valor | currency($store.state.moeda)}} </td>
                                            <td style="white-space: nowrap">{{parcela.valor_pago | currency($store.state.moeda)}} </td>
                                            <td style="white-space: nowrap">{{(parcela.valor - parcela.valor_pago) | currency($store.state.moeda)}} </td>
                                            <td>{{parcela.data_vencimento | dateformat}} </td>                                            
                                            <td>
                                                {{parcela.valor_pago == parcela.valor ? 'Pago' : (parcela.valor_pago == 0 ? 'Pendente' : 'Parcial')}}
                                                <a 
                                                    v-if="parcela.pagamentos.length > 0"
                                                    data-toggle="collapse" 
                                                    :href="'#collapse-pagamentos-parcela'+parcela.id" 
                                                    class="float-right link"
                                                    title="Ver Pagamentos"
                                                >
                                                    <span class="fa fa-eye"></span>
                                                </a>
                                            </td>
                                            <td>
                                                <span v-if="itemModal.parcela_relacionada" style="white-space: nowrap">
                                                    Saldo: {{(parcela.parcela_relacionada.valor - parcela.parcela_relacionada.valor_pago) | currency($store.state.moeda)}}
                                                </span>
                                            </td>
                                            
                                        </tr>
                                        <tr class="collapse" :id="'collapse-pagamentos-parcela'+parcela.id" >
                                            <td></td>
