                                                <ul>                                                
                                                    <li v-for="pagamento in parcela.pagamentos" :key="pagamento.id">
                                                        Id: {{ pagamento.lancamento_id}} | 
                                                        <span v-if="pagamento.lancamento.deleted_at !== null">
                                                            Excluído em {{pagamento.lancamento.deleted_at | dateformat}}
                                                        </span>
                                                        <span v-else-if="pagamento.lancamento.estornado" >
                                                            Estornado em {{pagamento.updated_at | dateformat}}
                                                        </span>
                                                        <span v-else>
                                                            Válido
                                                        </span>
                                                        |
                                                        Data Pagamento: {{ pagamento.lancamento.data_lancamento | dateformat}}
                                                        |
                                                        Caixa: {{pagamento.lancamento.conta.nome}} ({{pagamento.lancamento.conta.forma_desc}})
                                                        |
                                                        Valor: {{pagamento.valor | currency($store.state.moeda)}} 

                                                    </li>
                                                </ul>
                                                
                                            </td>
