                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">Fechar</button>
                    <button type="button" class="btn btn-primary" v-if="itemModal.acao == 'baixar'" :disabled="!caixaBaixa || (caixaBaixa.conta_bancaria && !forma_desc) || somaParcelasSelecionadas <= 0"
                            @click="baixarParcelas()">
                        <i class="fa fa-save"></i> Baixar Parcelas
                    </button>
                    <button type="button" class="btn btn-danger" v-if="itemModal.acao == 'cancelar'" :disabled="!itemModal || !itemModal.queroCancelar"
                        @click="cancelarConta()">
                        <i class="fa fa-close"></i> Cancelar
                    </button>
